/* scss variables ////////////////////////////*/

$serif: et-book,"Numeral LF Serif","Han Songti GB","Noto Serif SC","Noto Serif KR",Batang,Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,serif,"Noto Emoji";
$kai: et-book,"Numeral LF Serif",Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,"Times New Roman","Han Kaiti GB",STKaiti,Kaiti,"Kaiti SC",Kai,FZKaiti,"Noto Serif SC","Noto Serif KR",Batang,cursive,serif,"Noto Emoji";
$sans: "Gill Sans", "Gill Sans MT", Avenir, -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI Variable", "Segoe UI", Arial, "Han Heiti GB", "Han Heiti", Heiti, Hei, STHeiti, sans-serif, "Noto Emoji";
/* "Biaodian Pro Serif GB", Sans GB */

/* css custom properties ////////////////////////////*/
:root * {
      /*text-align: left;*/
      text-rendering: optimizeLegibility;
      --serif: et-book,"Numeral LF Serif","Han Songti GB","Noto Serif SC","Noto Serif KR",Batang,Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,serif,"Noto Emoji";
      --kai: et-book,"Numeral LF Serif",Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,"Times New Roman","Han Kaiti GB",STKaiti,Kaiti,"Kaiti SC",Kai,FZKaiti,"Noto Serif SC","Noto Serif KR",Batang,cursive,serif,"Noto Emoji";
      --sans: "Gill Sans", "Gill Sans MT", Avenir, -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI Variable", "Segoe UI", Arial, "Han Heiti GB", "Han Heiti", Heiti, Hei, STHeiti, sans-serif, "Noto Emoji";
}

/* main ////////////////////////////*/

html {
    scroll-behavior: smooth;
}

body {
    background-color: #fff;
    color: #414141;
    font-family: $serif;
}

::selection {
    color: unset;
    background: unset;
    text-decoration-line: line-through;
    text-decoration-thickness: 1.2rem;
    text-decoration-style: solid;
    text-decoration-color: #f9ff0052;
    text-underline-position: auto;
}

::-moz-selection {
    color: unset;
    background: unset;
    text-decoration-line: line-through;
    text-decoration-thickness: 1.2rem;
    text-decoration-style: solid;
    text-decoration-color: #f9ff0052;
    text-underline-position: auto;
}

/* text ////////////////////////////*/

p:lang(zh),
p:lang(ja), span:lang(ja), {
    font-size: 1.2rem;
    margin-top: 1.2rem;
    margin-bottom: 1.5rem;
}

// section>p {
//     text-indent: 2.1em;
// }

/* code ////////////////////////////*/

code, kbd, pre, samp {
    font-size: 1.1rem !important;
}

h2 > code {
    background: #e4e4e485;
    margin: 0.2rem;
    font-size: 1.85rem !important;
}

.highlight {
    width: 80% !important;
}

p > code, li > code, h-char > code {
    background: #e4e4e485;
    margin: 0.2rem;
}

.highlight>.chroma>code {
    width: 95% !important;
    white-space:pre-wrap;
    word-break:break-all;
    word-wrap:break-word;
}

code {
    font-size: 1rem;
    white-space: pre-wrap;
    // word-break: break-all;
    // word-wrap: break-word;
}

code.break-all {
      word-break: break-all;
      overflow-wrap: break-word;
      -ms-word-break: break-all;
      word-wrap: break-word;
      -ms-hyphens: auto;
      -moz-hyphens: auto;
      -webkit-hyphens: auto;
      hyphens: auto;
}

pre.chroma {
    overflow-x: auto;
}

/* hyperlink ////////////////////////////*/

a {
    border-bottom: 1px solid #a4a4a4;
    text-decoration: none;
}

a:focus {
    border-bottom: 2px solid #000;
}

a:hover {
    color: #000;
    border-bottom: 2px solid #000;
    box-shadow: 0 0 50px #81818194, 0 0 20px #dadada24;
}

a.heading-anchor {
    display: none;
    visibility: collapse;
}

/* header ////////////////////////////*/

.brand-head {
    max-width: 20rem;
}

.subtitle {
    font-style: inherit;
    font-family: $kai;
}

div#contents-fixed {
    position: fixed;
    z-index: 100;
    width: 5rem;
    height: 1.3rem;
    top: 0;
    background: #fff;
    // backdrop-filter: blur(50px);
    opacity: 0.5;
    border-right: 1px solid #e0e0e0;
    border-left: 1px solid #e0e0e0;
    border-bottom: 1px solid #e0e0e0;
    font-family: $sans;
    text-align: center;
    line-height: 1.2;
    display: none;
}

div#contents-fixed:hover {
    // box-shadow: 0 0 5px #81818194, 0 0 5px #dadada24;
    font-weight: 800;
}

div#contents-toc {
    display: none;
}

nav#TableOfContents > ul, nav#TableOfContents > ul > li, nav#TableOfContents > li, nav#TableOfContents > ul > li > ul, nav#TableOfContents > ul > li > ul > li {
    list-style-type: none;
    padding-top: 0;
    padding-bottom: 0;
    width: unset;
}

nav#TableOfContents > ul > a, nav#TableOfContents > ul > li > a, nav#TableOfContents > li > a, nav#TableOfContents > ul > li > ul > a, nav#TableOfContents > ul > li > ul > li > a {
    border-bottom: none;
}

nav#TableOfContents {
    font-family: $sans;
    position: fixed;
    z-index: 200;
    width: 50%;
    background: #fff;
    color: #a5a5a5;
    // backdrop-filter: blur(50px);
    border: 1px solid #e0e0e0;
    line-break: anywhere;
    font-size: 0.8rem;
}

.toc-highlight {
    font-weight: 600;
    background: #fff;
    color: #000 !important;
}

a.go-back {
    border-bottom: none;
    position: absolute;
    left: -3rem;
    color: #9a989155;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, var(--sans);
    font-weight: 200;
    margin-top: -0.25rem;
}

a.go-back:hover, a.comments-arrow:hover {
    color: #000;
    box-shadow: none;
}

a.list-title:hover, a.link-reverse:hover {
    box-shadow: none;
}

h1 {
    font-size: 2.2rem;
    font-weight: 600;
    color: #000;
    line-height: 1.5;
}

/* headings ////////////////////////////*/

h2 {
    margin-top: 5.5rem;
    font-size: 1.5em;
    font-family: $sans !important;
    font-weight: 600;
    line-height: 1.5;
}

h3 {
    font-family: $sans !important;
}

h2, h3 {
    font-style: normal;
}

h2:hover > a.heading-anchor,
h3:hover > a.heading-anchor {
    display: inline;
    visibility: visible;
}

h3 {
    font-size: 1.2em;
}

/* reading scroll bar */

.top-scroll-bar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    display: none;
    width: 0;
    height: 3px;
    background: #000000;
    box-shadow: 0 0 100px #00000094, 0 0 30px #00000024;
}

/* decoration ////////////////////////////*/

strong {
    color: #000;
    font-size: 1.15rem;
    font-family: $sans;
    /*vertical-align: middle;*/
    top: 1px;
    position: relative;
}

article blockquote {
    padding-left: 10px;
    border-left: 1px solid #ccc;
}

blockquote {
    color: #707070;
    font-family: $kai;
}

hr {
    text-align: left;
    margin-left: 0;
    margin-top: 1.4rem;
    margin-bottom: 1.4rem;
    /*width: 75%;*/
    max-width: 45rem;
    border-style: solid none none none;
    border-color: #111;
}

kbd {
  border: 1px #111 solid;
  border-radius: 5px;
  padding-right: 2px;
  padding-left: 2px;
}

mark {
    background: #f0d9bb;
}

.right-date {
    text-align: right;
}

/* footnote ////////////////////////////*/

.footnotes hr {
    margin-top: 4.4em;
}

.footnotes ol {
    width: 55%;
    margin-top: 4.4rem;

    li {
        p {
            width: 100%;
            margin: 0;
            padding: 0;

            font-size: 1rem;
    		font-family: "Biaodian Pro Serif GB",et-book,"Numeral LF Serif",Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,"Times New Roman","Han Kaiti GB",STKaiti,Kaiti,"Kaiti SC",Kai,FZKaiti,"Noto Serif SC","Noto Serif KR",Batang,cursive,serif;

            a.footnote-backref {
                font-size: 1.2rem;
                text-decoration: none;
            }
        }
    }
}

/* marginnote, sidenote ////////////////////////////*/

.marginnote .marginnote-ind,
.sidenote .sidenote-number {
    margin-right: 5px;
}

.sidenote, .marginnote {
    margin-bottom: 1rem;
    font-family: $kai;
    word-wrap: break-word !important;
}

.sidenote code,
.marginnote code {
    font-size: 0.9rem;
}

.newthought {
    font-variant: unset;
    font-size: unset;
}

/* list ////////////////////////////*/

li {
    font-size: 1.2rem;
    line-height: 1.5;
}

li > p, li > blockquote > p, li > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > blockquote > blockquote > p {
    width: inherit;
}

li>ol {
    width: 100%;
}

/* han.css adjust ////////////////////////////*/

h-cs {
    letter-spacing: 0.1ch !important;
    font-size: unset !important;
}

/* iframe ////////////////////////////*/

iframe {
    width: 58% !important;
    max-width: 58% !important;
}

/* math ////////////////////////////*/

mjx-container[jax="CHTML"][display="true"],
.katex-display {
      overflow: auto;
      overflow-y: hidden;
/*     -ms-overflow-style: none;
    scrollbar-width: none; */
}

/* footer ////////////////////////////*/

.link-reverse, footer.page-footer a, .menu li a {
    border: none;
    color: #414141 !important;
}

.link-reverse:hover, footer.page-footer a:hover, .menu li a:hover {
    color: #000 !important;
    border-bottom: 1px solid #a4a4a4;
}

footer.page-footer ul.page-footer-menu {
    margin-top: 2rem !important;
}

footer.page-footer p {
    font-size: 1rem !important;
}

rt {
    font-size: 0.7em;
}

/* list-page ////////////////////////////*/

.list-page {
    ul {
        list-style-type: none;
        margin: -0.25em;
        width: 87.5%;
        max-width: 75rem;
    }
    li {
        margin: 0;
        /*font-size: 95%;*/
    }
}

.list-page .list-date {
    display: inline;
    font-size: 0.75em;
    /* padding-right: 2em; */
    /* margin-right: 2em; */
}

.list-date, .date {
    color: #9a9891;
}

.year {
    padding-top: 1rem;
}

.list-title {
    border: none;
    font-size: 1.25rem;
    font-weight: bolder;
    /*line-height: 1.5;*/
}

.list-title:hover {
    color: #000 !important;
    border-bottom: 1.3px solid #000;
}

/* list-page animation ////////////////////////////*/

:root {
    --animate-duration: 1s;
    --animate-delay: 1s;
    --animate-repeat: 1
}

.animate__animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: var(--animate-duration);
    animation-duration: var(--animate-duration);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both
}

.animate__animated.animate__faster {
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-duration: calc(var(--animate-duration)/2);
    animation-duration: calc(var(--animate-duration)/2)
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0,100%,0);
        transform: translate3d(0,100%,0)
    }

    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0)
    }
}

.animate__fadeInUp {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp
}

/* list-page table ////////////////////////////*/

table:not(.lntable) {
    margin-top: 1.4em;
    font-size: 1.4rem;
    width: auto; /* making booktabs style tables the unstyled default in case someone uses Markdown styling */
    /* margin: 0 auto; */
    /* border-spacing: 0px; */
}

table:not(.lntable) tr th {
  border-bottom: 1px solid #111;
  text-transform: uppercase;
}

table:not(.lntable) tr th,
table:not(.lntable) tr td {
    padding-right: 0.5rem;
}

table.lntable {
    td.lntd {
        padding: 0em;
    }
    border-spacing: 0;
    padding: 0;
}

table:not(.lntable) tr th, table:not(.lntable) tr td {
    text-align: center;
    font-size: 1.2rem;
    line-height: 1.5;
}

/* screen ////////////////////////////*/

@media screen and (max-width: 760px) {
    p:lang(zh),
    p:lang(ja), span:lang(ja), li, strong {
        font-size: 1.12rem;
    }
    #main li, dt, dd {
        padding: 0;
    }
    code, kbd, pre, samp {
        font-size: 1.1rem !important;
    }
    h1 {
        width: 95%;
    }
    h2, h3, p, footer {
        width: 85%;
    }
    /*article li, article p {
        text-align: left !important;
    } */ 
    iframe {
        width: 85% !important;
        max-width: 85% !important;
        overflow: hidden;
    }
    .sidenote, .marginnote {
        margin: 1.3em;
        font-size: smaller;
    }
    .footnotes ol {
        width: 90%;
    }
    .list-page ul {
        width: 100%;
    }
    /*.list-page ul > li {
        text-align: left;
    }*/
    .highlight>.chroma>code {
        width: 95% !important;
        white-space: unset !important;
        word-break: unset !important;
        word-wrap: unset !important;
    }
    div#waline {
        width:  85%;
    }
    code {
        overflow-wrap: break-word;
        word-wrap: break-word;
        -ms-word-break: break-all;
        word-break: break-word;
        -ms-hyphens: auto;
        -moz-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;
    }
    strong {
        top: 0px;
    }
}

@media screen and (min-width: 1080px) {
    strong {
        position: relative;
        top: 0.08rem;
    }
    nav#TableOfContents {
        width: 35%;
    }
}

/* dark mode ////////////////////////////*/

@media (prefers-color-scheme: dark) {
    body {
        background-color: #000;
        color: #e8eaed;
    }
    h1, h2, h3 {
        color: #fff;
    }
    .link-reverse, footer.page-footer a, .menu li a {
        color: #e8eaed !important;
    }
    strong {
        color: #fff;
    }
    blockquote {
        color: #aaaaa6;
    }
    hr {
        border-color: #e8eaed;
    }
    a:focus {
        border-bottom: 2px solid #fff;
    }
    a:hover {
        color: #fff !important;
        border-bottom: 2px solid #fff;
        box-shadow: 0 0 50px #ffffff94, 0 0 30px #ffffff24;
        background-color: #000;
    }
    .list-title:hover {
        color: #fff !important;
        border-bottom: 1.3px solid #fff;
    }
    .link-reverse:hover, footer.page-footer a:hover, .menu li a:hover {
        color: #fff !important;
        border-bottom: 1px solid #a4a4a4;
    }
    a.go-back {
        color: #cacaca6e;
    }
    a.go-back:hover, a.comments-arrow:hover {
        color: #fff;
        border-bottom: none;
    }
    .top-scroll-bar {
        background: #fff;
        box-shadow: 0 0 50px #ffffff94, 0 0 30px #ffffff94;
    }
    div#contents-fixed {
        background: #000;
    }
    nav#TableOfContents {
        background: #000;
    }
}


/* basically dont need to change these ////////////////////////////*/
/* cols */

.row {
  /* mobile */
  display: grid;
  width: 75%;
  gap: 0.3rem;
  margin: 0 0 1rem 0;
}

.row .column:nth-of-type(2n) {
  margin-left: 1rem;
}

.row .column:nth-of-type(3n) {
  margin-left: 2rem;
}

.row .column > p:first-child {
  margin-top: 0;
}

.row .column > p:last-child {
  margin-bottom: 0;
}

@media (min-width: 760px) {
  /* desktop */
  /* https://stackoverflow.com/a/62086985 */
  .row {
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    /* above 2 lines are equivalent as below 1 line */
    /* grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); */
    gap: 1.4rem;
    margin: 0 0 1.4rem 0;
  }
  .row .column {
    margin-left: 0 !important;
  }
}

.column > p {
  width: 100%;
}

.column > p > label.margin-toggle.marginnote-ind {
  display: inline;
}

.column > p > .sidenote,
.column > p > .marginnote {
  display: none;
}

.column > p > .margin-toggle:checked + .sidenote,
.column > p > .margin-toggle:checked + .marginnote {
  display: block;
  float: left;
  left: 1rem;
  clear: both;
  width: 95%;
  margin: 1rem 2.5%;
  vertical-align: baseline;
  position: relative;
}

/* button */

.btn {
    display: inline-block;
    margin-top: 1.4rem;
}
.btn button {
    font-size: 1.4rem;
}
.btn button .icon {
    font-size: 1.2rem;
}

/* no select */

.no-select {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari, Chrome, Opera, Samsung */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Edge, IE */
    user-select: none; /* Modern browsers */
}